<template>
  <div class="mobile">
    <router-view />
  </div>
</template>

<script>
export default {
  computed: {
    device_type() {
      return this.$store.state.app.device_type;
    },
  },
  created() {
    if (this.device_type != "mobile") {
      this.$router.push("/");
    }
  },
};
</script>

<style lang="scss" scoped>
.mobile {
  width: 100%;
  height: 100%;
}
</style>


